@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;600&display=swap');


@media (max-width: 992px) {
 .navbar-collapse, .collapse {
  display: flex;
  justify-content: flex-start;
 }
 .logo {
  padding-left: 15px;
  font-size: 36px !important;
 }
 .show{
  display: block !important;
 }
 .navigation_item {
  margin: 10px 0 10px 0;
 }
 nav {
  margin-top: 0 !important;
 }
 .title {
  padding-left: 15px;
  padding-right: 15px;
  font-size: 38px !important;
 }
 .subtitle {
  display: none;
 }
}


.full_overlay {
 position: absolute;
 height: 100vh;
 background-image: url('./images/apple.png');
 background-repeat: no-repeat;
 background-size: cover;
 height: 700px;
 width: 100%;
}
.main_contant {
 position: relative;
 max-width: 1170px;
 margin: 0 auto;
}
nav {
 margin-top: 60px;
}
.navbar-collapse, .collapse {
 display: flex;
 justify-content: flex-end;
}

.logo {
 font-size: 52px;
 font-family: "Lato";
 color: rgb(255, 255, 255);
}

.navigation_item {
 margin-right: 10px;
 font-size: 25px;
 font-family: "OpenSans", sans-serif;
 color: rgb(255, 255, 255);
 line-height: 0.306;
 text-align: left;
}

.title {
 margin-top: 10px;
 font-size: 60px;
 font-family: "OpenSans", sans-serif;
 color: rgb(255, 255, 255);
 font-weight: bold;
 line-height: 1.761;
 text-align: center;
 letter-spacing: 5px;
}
.subtitle {
 font-size: 35px;
 font-family: "OpenSans", sans-serif;
 color: rgb(255, 255, 255);
 font-weight: bold;
 line-height: 1.761;
 text-align: center;
 letter-spacing: 1px;
}

.input {
 border-style: solid;
 border-width: 1px;
 border-color: rgb(255, 255, 255);
 background-color: rgb(255, 255, 255);
 width: 349px;
 height: 63px;
}

.start {
  border-style: solid;
  border-width: 1px;
  border-color: rgb(255, 255, 255);
  background-color: rgb(186, 39, 31);
  width: 349px;
  height: 63px;
}
.form_elements {
 margin-top: 40px;
}